import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'
import authService from "../services/auth.service";
import AddField from "./AddField";


const required = (value) => {
    if (!value) {
         return (
              <div className="alert alert-danger" role="alert">
                   This field is required!
              </div>
         );
    }
};

export default class EditTemplate extends Component {

    constructor(props) {
        super(props);
        this.AdoptSurvey = this.AdoptSurvey.bind(this);
        this.onChangeSurvey = this.onChangeSurvey.bind(this);

        this.state = {
            loading: false,
            message: "",
            myloading: false,
            ApproveSurveymessage: "",
            ApproveSurveysuccessful: false,
            ApproveSurveyloading: false,
            survey:"",


        };
        
    }
    
    
    onChangeSurvey(e) {
        this.setState({
            survey: e.target.value
        });
      }

    AdoptSurvey(e) {
        e.preventDefault();

        this.setState({
            ApproveSurveymessage: "",
            ApproveSurveysuccessful: false,
            ApproveSurveyloading: true
        });
        
            //check auth user
            const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
            const token = mytoken.token;

            //get template details
            const ExtractTemplate = JSON.parse(localStorage.getItem('Templatedetails'));
            const ExtractTemplatePk = ExtractTemplate.pk;

            //loop through the json
            const formList = [];
            for (let i = 0; i < ExtractTemplate.form.length; i++) {
                formList.push(ExtractTemplate.form[i]);
            }
             // newly created json
            const body =
            {
                survey_template_pk:ExtractTemplatePk,
                survey_pk: this.state.survey,
                form: formList,
                
            } 
            // console.log(body);
            this.form.validateAll();
            if (this.checkBtn.context._errors.length === 0) {
    
            axios.post("https://gfso.chinikiguard.com/survey/api/adopt/survey/", body,
                {
                    headers:
                    {
                        'Content-Type': 'application/json',
                        'Authorization': `Token ${token}`,
                        'Access-Control-Allow-Origin': '*',
                        'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                        'Access-Control-Allow-Credentials': true
                    },
                })
                .then(res => {
                    this.setState({
                        ApproveSurveymessage: res.data.detail,
                        ApproveSurveysuccessful: true,
                    });
                    window.location = "/ViewSurvey"
                    if (res.data) {
                        localStorage.setItem("AdoptedSurvey", JSON.stringify(res.data));
                    }
                },
                error => {
                    const resMessage =
                        (error.response &&
                            error.response.data.detail &&
                            error.response.data.detail) ||
                        error.ApproveSurveyloading ||
                        error.toString();

                    this.setState({
                        ApproveSurveyloading: false,
                        message: resMessage
                    });
                    }
                );
        } else {
            this.setState({
                ApproveSurveyloading: false
            });
        }
    }

    

    componentDidMount() {
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://gfso.chinikiguard.com/survey/api/list/survey/?approved=yes&deployed=no",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("ApprovedSurveyToUser", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
                this.setState({
                    ApprovedSurveyToUser: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })
    }

    

    render() {

        //check for user to access the page
        if (!localStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        //get the template details
        const SingleTemplate = JSON.parse(localStorage.getItem('Templatedetails'));
        const SingleTemplateCategories = SingleTemplate.form;
        const TemplateTitle= SingleTemplate.title;
        

        return (
            <div>
                <Header />
                <div className="content-wrapper" style={{ minHeight: 1602 }}>
                    <Menu /><br />
                    <section className="content">
                        <div className="container-fluid">
                            <div className="row">
                                {/* left column */}
                                <div className="col-md-12">
                               
                                    <center>
                                        {this.state.ApproveSurveymessage && (
                                            <div className="pb-5" >
                                                <div
                                                    className={
                                                        this.state.ApproveSurveysuccessful
                                                            ? "alert alert-success"
                                                            : "alert alert-danger"
                                                    }
                                                    role="alert"
                                                >
                                                    <h3 > {this.state.ApproveSurveymessage}</h3>
                                                </div>
                                            </div>
                                        )}</center>
                                                     
                                <center><h4>{TemplateTitle}</h4></center>

                                    {/* right column/ hannun dama */}
                                    {/* /.card */}
                                    {SingleTemplateCategories&&SingleTemplateCategories.map( (Templatefields ,i) => (
                                        <div className="card card-">
                                            <div className="card-header">
                                                <h3 className="card-title">{Templatefields.economic_code}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<b>{Templatefields.name}</b>
                                                </h3>
                                                {/* <h6 className="">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{Templatefields.type}</h6> */}
                                                <h6 className=""style={{ fontStyle: 'italic'}}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{Templatefields.heading}</h6>

                                            </div>
                                            <div className="card-body">
                                                {/* <h6 className="">{Templatefields.economic_code}</h6> */}
                                                {Templatefields.sub_categories.map(subCategory => (
                                                    <div>
                                                        <h6 className="">{subCategory.name}</h6>

                                                        <div className="row">
                                                            {subCategory.fields.map(SubCategoryfields => (
                                                                <div className="col-4">
                                                                    <h6 className="">{SubCategoryfields.economic_code}</h6>
                                                                    <h6 className="">{SubCategoryfields.type}</h6>
                                                                    <h6 className="">{SubCategoryfields.heading}</h6>

                                                                    <input type="text" className="form-control is-valid" placeholder={SubCategoryfields.name} disabled  />                                                <br />
                                                                </div>
                                                            ))}

                                                        </div>
                                                       
                                                    </div>
                                                ))}
                                                <hr/>
                                                <div className="row">
                                                    {Templatefields.fields.map((fields,i) => (

                                                 <div className="col-4">
                                                <h6 className="">{fields.economic_code}</h6>
                                                <h6 className="">{fields.type}</h6>
                                                <h6 className="">{fields.heading}</h6>
                                                            <input type="text" className="form-control is-valid" placeholder={fields.name} disabled  />                                                <br />

                                              </div>
                                                       
                                                                                                          
                                                    ))}
                                                </div>
                                                <AddField key={i} field={Templatefields} MyCategoryName={Templatefields.name} ApprovedSurveyToUser={this.state.ApprovedSurveyToUser} />   
   
                                            </div>
                                            {/* /.card-body */}
                                        </div>
                                    ))}
                                     
                                    {/* /.card */}
                                    {SingleTemplateCategories.length ? 

                                    <div className="card card-">
                                        <div className="card-header">
                                            <h3 className="card-title">Action</h3>
                                        </div>
                                        <div className="card-body">
                                        
                                        <Form onSubmit={this.AdoptSurvey} ref={c => { this.form = c; }} > 
                                        {!this.state.ApproveSurveysuccessful && (
                                                       <div>
                                                    <div className="form-group">
                                                        <Select
                                                            className="form-control"
                                                            id="exampleFormControlSelect9"
                                                              validations={[required]}
                                                            onChange={this.onChangeSurvey}
                                                            value={this.state.survey}
                                                        >
                                                        <option value="">--select--</option>

                                                            {this.state.ApprovedSurveyToUser && this.state.ApprovedSurveyToUser.map(survey => (

                                                                <option value={survey.pk}>{survey.title}</option>

                                                            ))}

                                                        </Select>
                                                    </div>
                                       
                                        <center>
                                                

                                        {/* <AddField />    */}

                                                  <button
                                                      disabled={this.state.ApproveSurveyloading}
                                                      className="btn btn-success btn-lg "
                                                      onClick={this.props.saveField}
                                                  >

                                                      {this.state.ApproveSurveyloading && (
                                                          <span className="spinner-grow spinner-grow-sm"></span>
                                                      )}
                                             {/* <AddField  />    */}

                                                      <span>Develop Template</span>
                                                  </button>
                                                  </center>

                                                  <center>
                                        {this.state.ApproveSurveymessage && (
                                            <div className="pb-5" >
                                                <div
                                                    className={
                                                        this.state.ApproveSurveysuccessful
                                                            ? "alert alert-success"
                                                            : "alert alert-danger"
                                                    }
                                                    role="alert"
                                                >
                                                    <h3 > {this.state.ApproveSurveymessage}</h3>
                                                </div>
                                            </div>
                                        )}</center>
               
                                                 

                                             

                                          <CheckButton
                                              style={{ display: "none" }}
                                              ref={c => {
                                                  this.checkBtn = c;
                                              }}
                                          />
                                    </div>
                                        )}
                                      </Form> 
                                        
                                          
                                       
                                        
                                        </div>
                                                                                
                                    </div>
                                    : <center><h2>No Template Available</h2></center>
                                        
                                }
                                    {/* /.card-body */}
                                </div>
                                {/* /.card */}
                            </div>
                            {/*/.col (right) */}
                            {/* /.row */}
                        </div>{/* /.container-fluid */}
                    </section>
                </div>
                <Footer />
            </div>
        );
    }
}



