const AddField = (props) => {


  
  const [inputList, setInputList] = useState([{ name: "", economic_code: "" }]);

  const [successful, setSuccessful] = useState(false);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target;
    const list = [...inputList];
    list[index][name] = value;
    setInputList(list);
  };

  // handle click event of the Remove button
  const handleRemoveClick = index => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([...inputList, { name: "", economic_code: "" }]);
  };

  const handleSave = () => {

    //login user token
    const mytoken = JSON.parse(localStorage.getItem('_WannanGfsoCbnUserNe'));
    const token = mytoken.token;

    // Existing template from localStorage
    const ExtractTemplate = JSON.parse(localStorage.getItem('Templatedetails'));
    const ExtractTemplatePk = ExtractTemplate.pk;

    //his  fields from template
    const twist_result = [];
    for (let i = 0; i < ExtractTemplate.form.length; i++) {
      twist_result.push(ExtractTemplate.form[i]);
    }

    localStorage.setItem("result", JSON.stringify(twist_result));

    // console.log(inputList)
    // my custom fields
    const fieldsList = [];
    for (let i = 0; i < inputList.length; i++) {
      fieldsList.push(inputList[i]);
    }

    // console.log(props.MyCategoryName)

    // newly fields added to selected category from user
    const MyCategoryName = props.MyCategoryName;
    const result = twist_result.find(({ name }) => name === MyCategoryName);


    const modifiedObj = twist_result.filter(item => item.name !== MyCategoryName);



    const fieldsObj = modifiedObj.concat(result);
    // console.log("fieldsObj:", fieldsObj);
    const last_element = fieldsObj[fieldsObj.length - 1];
    // console.log("last_element:", last_element);
    const final = last_element.fields
    const name = last_element.name;
    const economic_code = last_element.economic_code;
    const heading = last_element.heading;
    const type = last_element.type;
    const finalup = final.concat(inputList);


    const my_category = {
      name,
      economic_code,
      heading,
      type,
      fields:
        finalup,

    }
    // console.log("my_category:", my_category);

    const form = modifiedObj.concat(my_category)
    // console.log("twist_result:", twist_result);

   const survey_pk= props.ApprovedSurveyToUser
    // final result to  be send to the endpoint
    const body =
    {
      survey_template_pk: ExtractTemplatePk,
      survey_pk: survey_pk[0].pk,
      form,


    }
    console.log(body)

    axios.post("https://gfso.chinikiguard.com/survey/api/adopt/survey/", body,
    {
      headers:
      {
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
      },
  }
    )
      .then(res => {
        // window.location = "/ViewSurvey"
        // if (res.data) {
        //   localStorage.setItem("AddedField", JSON.stringify(res.data));
        // }
        setMessage(res.data.detail);
        setSuccessful(true);
      },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data.detail &&
              error.response.data.detail) ||
            error.message.message ||
            error.toString();

          setSuccessful(false);
          setLoading(false);
          setMessage(resMessage);
        }
      );



  };

  return (
    <div >


      {inputList.map((x, i) => {
        return (
          <div >
            <hr/>
            <h6>Add field (optional)</h6>
            <div className="row">

              <div className="col-4">

                <input
                  className="form-control"
                  name="name"
                  placeholder="Enter Field Name"
                  value={x.name}
                  onChange={e => handleInputChange(e, i)}
                />

              </div>
              <div className="col-4">
                <input
                  type="number"
                  className="form-control"
                  name="economic_code"
                  placeholder="Enter economic code"
                  value={x.economic_code}
                  onChange={e => handleInputChange(e, i)}
                   />
              </div>

            </div>

               &nbsp;
            <div className="btn-box">
              {inputList.length !== 1 && <button

                className="btn btn-danger"
                onClick={() => handleRemoveClick(i)}>
                -
                   </button>
              }
                   &nbsp;&nbsp;
                 {inputList.length - 1 === i &&
                <button
                  className="btn btn-primary"

                  onClick={handleAddClick}>Add more &nbsp;+
                 </button>
              }
                 &nbsp;&nbsp;
                 {inputList.length - 1 === i &&
                <button
                  className="btn btn-success"

                  onClick={handleSave}>Append&nbsp;
                 </button>
                 }

              {message && (
                <div className="form-group">
                  <div
                    className={
                      successful ? "alert alert-success" : "alert alert-danger"
                    }
                    role="alert"
                  >

                    {message}
                  </div>
                </div>

              )}
            </div>
          </div>
        );
      })}


      {/* <div style={{ marginTop: 20 }}>{JSON.stringify(inputList)}</div> */}

    </div>

  );




  //      const [formValues, setFormValues] = useState([{ name: "", email : ""}])

  //     let handleChange = (i, e) => {
  //         let newFormValues = [...formValues];
  //         newFormValues[i][e.target.name] = e.target.value;
  //         setFormValues(newFormValues);
  //       }

  //     let addFormFields = () => {
  //         setFormValues([...formValues, { name: "", email: "" }])
  //       }

  //     let removeFormFields = (i) => {
  //         let newFormValues = [...formValues];
  //         newFormValues.splice(i, 1);
  //         setFormValues(newFormValues)
  //     }

  //     let handleSubmit = (event) => {
  //         event.preventDefault();
  //         alert(JSON.stringify(formValues));
  //     }

  //     return (
  //         <form  onSubmit={handleSubmit}>
  //           {formValues.map((element, index) => (
  //             <div className="form-inline" key={index}>
  //               <label>Name</label>
  //               <input type="text" name="name" value={element.name || ""} onChange={e => handleChange(index, e)} />
  //               <label>Email</label>
  //               <input type="text" name="email" value={element.email || ""} onChange={e => handleChange(index, e)} />
  //               {
  //                 index ? 
  //                   <button type="button"  className="button remove" onClick={() => removeFormFields(index)}>Remove</button> 
  //                 : null
  //               }
  //             </div>
  //           ))}
  //           <div className="button-section">
  //               <button className="button add" type="button" onClick={() => addFormFields()}>Add</button>
  //               <button className="button submit" type="submit">Submit</button>
  //           </div>
  //       </form>
  //     )


}

export default AddField
