import React, { Component } from "react";


export default class test extends Component {

     constructor() {
          super()
      
          this.state = {
            data: [{
              "name": "bin"
            }, {
              "name": "lu"
            }
            ]
          }
        }
      
        onChange = (e, idx) => {
          this.setState({
            data: this.state.data.map((o,i) => {
              if(i === idx){
                return e.target.value
              }
              return o
            })
          })
        }
      
        getAllStates = () => console.log(JSON.parse(this.state))
      
        render() {
          return(
            <div>{this.state.data.map((o,i)=>{
              return <div><br />
               <textarea onChange={e => this.onChange(e, i)}>
                {JSON.stringify(o)}
               </textarea>
               </div>
            })}
            
            <br />
            <button onClick={this.getAllStates}>Save JSON</button>
      
            </div>
          )
        }
      }
